// models import
import '@tensorflow/tfjs-backend-cpu';
import * as cocoSsd from '@tensorflow-models/coco-ssd';
import {DetectedObject, ObjectDetection} from '@tensorflow-models/coco-ssd';
import {AIObjectDetectionActions} from '../logic/actions/AIObjectDetectionActions';
import {AIFormDetectionActions} from '../logic/actions/AIFormDetectionActions';
import {AIMaskDetectionActions} from '../logic/actions/AIMaskDetectionActions';
import {IPoint} from '../interfaces/IPoint';

// import { exportComponentAsJPEG} from 'react-component-export-image';

import {store} from '../index';
import {updateMaskDetectorStatus} from '../store/ai/actionCreators';
import {LabelType} from '../data/enums/LabelType';
import {LabelsSelector} from '../store/selectors/LabelsSelector';

import {updateActiveLabelType} from '../store/labels/actionCreators';

// import React, {useState, useEffect} from 'react';

export interface DetectedMask {
    vertices: IPoint[];  // [x, y, width, height]
    class: string;
    score: number;
  }

export class MaskDetector {
    // private static model: ObjectDetection;

    public static loadModel(callback?: () => any) {

        console.log("I am in mask fucker")
        store.dispatch(updateMaskDetectorStatus(true));
        store.dispatch(updateActiveLabelType(LabelType.POLYGON));
        const activeLabelType: LabelType = LabelsSelector.getActiveLabelType();
        if (activeLabelType === LabelType.POLYGON) {
            AIMaskDetectionActions.detectPolygonsForActiveImage();
        }
        if (callback) {
            callback();
        }
    }

    public static predict(image: HTMLImageElement, callback?: (predictions: DetectedMask[]) => any) {
        async function submit(elephant){
            // var elephant = image;
            var imgCanvas = document.createElement("canvas");
            var imgContext = imgCanvas.getContext("2d");
            
            // Make sure canvas is as big as the picture
            imgCanvas.width = elephant.width;
            imgCanvas.height = elephant.height;
    
            // Draw image into canvas element
            imgContext.drawImage(elephant, 0, 0, elephant.width, elephant.height);
            var imageBlob = new Blob();
            console.log(imageBlob);

            imageBlob = await new Promise(resolve => imgCanvas.toBlob(resolve, 'image/png'));
            // let imageBlob = imgCanvas.toBlob(resolve, 'image/png'));
            console.log(imageBlob);
            var formData = new FormData();
            // formData.append("firstName", "John");
            formData.append("image", imageBlob, "image.png");
            console.log("formdata", formData);
            console.log(formData.get('image'));
            let response = await fetch('/upload', {
              method: 'POST',
              body: formData
            });
        }

        submit(image)
        .then(()=>{fetch('/mask')
            .then(function (response) {
                return response.json();
            }).then((predictions: DetectedMask[]) => {
                console.log('preds');
                console.log(predictions) 
                if (callback) {
                    callback(predictions)
                }
            })
                .catch((error) => {
                    // TODO
                    throw new Error(error as string);
                }) ;})           
           
    }
    
}
