import {MaskDetector} from '../../ai/MaskDetector';
import {DetectedMask} from '../../ai/MaskDetector';
import {IPoint} from '../../interfaces/IPoint';

import {ImageData, LabelName, LabelPolygon} from '../../store/labels/types';
import {LabelsSelector} from '../../store/selectors/LabelsSelector';
import { v4 as uuidv4 } from 'uuid';
import {store} from '../../index';
import {updateImageDataById} from '../../store/labels/actionCreators';



import {ImageRepository} from '../imageRepository/ImageRepository';
import {LabelStatus} from '../../data/enums/LabelStatus';
import {findLast} from 'lodash';
import {updateSuggestedLabelList} from '../../store/ai/actionCreators';
import {PopupWindowType} from '../../data/enums/PopupWindowType';
import {updateActivePopupType} from '../../store/general/actionCreators';
import {AISelector} from '../../store/selectors/AISelector';
import {AIActions} from './AIActions';

export class AIMaskDetectionActions {
    public static detectPolygonsForActiveImage(): void {
        const activeImageData: ImageData = LabelsSelector.getActiveImageData();
        AIMaskDetectionActions.detectPolygons(activeImageData.id, ImageRepository.getById(activeImageData.id))
    }

    public static detectPolygons(imageId: string, image: HTMLImageElement): void {
        if (LabelsSelector.getImageDataById(imageId).isVisitedByMaskDetector || !AISelector.isAIMaskDetectorModelLoaded())
            return;

        store.dispatch(updateActivePopupType(PopupWindowType.LOADER));
        MaskDetector.predict(image, (predictions: DetectedMask[]) => {
            const suggestedLabelNames = AIMaskDetectionActions.extractNewSuggestedLabelNames(LabelsSelector.getLabelNames(), predictions);
            const rejectedLabelNames = AISelector.getRejectedSuggestedLabelList();
            const newlySuggestedNames = AIActions.excludeRejectedLabelNames(suggestedLabelNames, rejectedLabelNames);
            if (newlySuggestedNames.length > 0) {
                store.dispatch(updateSuggestedLabelList(newlySuggestedNames));
                store.dispatch(updateActivePopupType(PopupWindowType.SUGGEST_LABEL_NAMES));
            } else {
                store.dispatch(updateActivePopupType(null));
            }
            AIMaskDetectionActions.savePolygonPredictions(imageId, predictions);
        })
    }

    public static savePolygonPredictions(imageId: string, predictions: DetectedMask[]) {
        const imageData: ImageData = LabelsSelector.getImageDataById(imageId);
        const predictedLabels: LabelPolygon[] = AIMaskDetectionActions.mapPredictionsToPolygonLabels(predictions);
        const nextImageData: ImageData = {
            ...imageData,
            labelPolygons: imageData.labelPolygons.concat(predictedLabels),
            isVisitedByMaskDetector: true
        };
        store.dispatch(updateImageDataById(imageData.id, nextImageData));
    }

    private static mapPredictionsToPolygonLabels(predictions: DetectedMask[]): LabelPolygon[] {
        return predictions.map((prediction: DetectedMask) => {
            // var nodes = IPoint[]
            return {
                id: uuidv4(),
                labelIndex: null,
                labelId: null,
                vertices: prediction.vertices,
                isCreatedByAI: true,
                status: LabelStatus.UNDECIDED,
                suggestedLabel: prediction.class
            }
        })
    }

    public static extractNewSuggestedLabelNames(labels: LabelName[], predictions: DetectedMask[]): string[] {
        return predictions.reduce((acc: string[], prediction: DetectedMask) => {
            if (!acc.includes(prediction.class) && !findLast(labels, {name: prediction.class})) {
                acc.push(prediction.class)
            }
            return acc;
        }, [])
    }

    public static acceptAllSuggestedPolygonLabels(imageData: ImageData) {
        const newImageData: ImageData = {
            ...imageData,
            labelPolygons: imageData.labelPolygons.map((labelPolygon: LabelPolygon) => {
                const labelName: LabelName = findLast(LabelsSelector.getLabelNames(), {name: labelPolygon.suggestedLabel});
                return {
                    ...labelPolygon,
                    status: LabelStatus.ACCEPTED,
                    labelId: !!labelName ? labelName.id : labelPolygon.labelId
                }
            })
        };
        store.dispatch(updateImageDataById(newImageData.id, newImageData));
    }

    public static rejectAllSuggestedPolygonLabels(imageData: ImageData) {
        const newImageData: ImageData = {
            ...imageData,
            labelPolygons: imageData.labelPolygons.filter((labelPolygon: LabelPolygon) => labelPolygon.status === LabelStatus.ACCEPTED)
        };
        store.dispatch(updateImageDataById(newImageData.id, newImageData));
    }
}
